import { motion } from "framer-motion";
import { useState } from "react";

const Tree = ({ onShake }) => {
  const [shakeKey, setShakeKey] = useState(0);

  const handleShake = () => {
    setShakeKey((prev) => prev + 1); // Tng key  ti to hiu ng rung
    onShake();
  };

  return (
    <motion.img
      key={shakeKey} // Dng key  reset animation
      src="/tree.png"
      alt="Cy mai"
      className="tree"
      initial={{ rotate: 0 }}
      animate={{ rotate: [-5, 5, -5, 5, 0] }}
      transition={{ duration: 0.5 }}
      onClick={handleShake}
      style={{ width: "200px" }}
    />
  );
};

export default Tree;
